import * as PIXI from 'pixi.js'
import * as gameObjects from './gameObjects'

const app = new PIXI.Application({
    backgroundColor : 0x90EE90,
});
function init(){
    gameObjects.gameStage.create();
    gameObjects.gameMap.create();
    gameObjects.bar.create();
    gameObjects.scoreBoard.create();
    gameObjects.skillArea.create();
    gameObjects.gameMap.shuffleAll(0);
}
function createApp(){
    document.body.appendChild(app.view);
    app.renderer.view.style.position='absolute'
    app.renderer.view.style.display ='block';
    app.renderer.autoResize = true;
    app.renderer.resize(window.innerWidth,window.innerHeight);
    app.stage.interactive = true;
    init();
}

export {createApp,app}