import * as classTile from './classTile'
import * as PIXI from "pixi.js"
import * as gameSystem from './gameSystem'

let gameStage = {
    itself : new PIXI.Container(),
    positionX : window.innerWidth/3,
    positionY : 0,
    create(){
        this.itself.position.set(this.positionX, this.positionY);
        gameSystem.app.stage.addChild(this.itself);
    }
}

let gameMap={
    //想不到合适的数
    startX : 0,
    startY : window.innerHeight/2-classTile.Tile.lengthY,
    itself : new PIXI.Graphics(),
    tileLists : new Map(),
    typeNumbers : new Array(20).fill(0),
    create(){
        for(let i=1;i<=classTile.Tile.types;i++){
            this.typeNumbers[i]=0;
        }
        this.itself.beginFill(0x66CCFF);
        this.itself.position.set(this.startX, this.startY);
        this.itself.drawRect(0,0, classTile.Tile.lengthX*6,classTile.Tile.lengthY*5);
        this.itself.zIndex = 5;
        gameStage.itself.addChild(this.itself);
    },
    fallAndCreate(X,Y){
        if(Y!==0){
            for(let i=Y-1;i>=1;i--){
                gameMap.tileLists.set(`${X},${i+1}`,gameMap.tileLists.get(`${X},${i}`));
                let tempTile =gameMap.tileLists.get(`${X},${i+1}`);
                tempTile.itself.position.set(classTile.Tile.toMapX(X),classTile.Tile.toMapY(i+1));
                tempTile.y++;
            }
            gameMap.tileLists.set(`${X},1`,new classTile.Tile(0));
            let tempTile =gameMap.tileLists.get(`${X},1`);
            tempTile.createToMap(X,1);
        }
    },
    shuffleAll(blockChoice){
        for(let i=1;i<=6;i++){
            for (let j=1;j<=5;j++){
                let isBlock = Math.floor(Math.random()*blockChoice);
                let id;
                if(isBlock===1){
                    id=17;
                }else {
                    id=0;
                }
                let tile = new classTile.Tile(id);
                tile.createToMap(i,j);
            }
        }
    },
    checkId(id){
        return this.typeNumbers[id]>=3;
    }
}

let bar={
    startX : gameMap.startX-classTile.Tile.lengthX/2,
    startY : gameMap.startY+classTile.Tile.lengthY*5 +50,
    itself : new PIXI.Graphics(),
    maxSize : 7,
    lengthNow : 0,
    tileLists : [''],
    typeNumbers : new Array(20).fill(0),
    create(){
        for(let i=1;i<=classTile.Tile.types;i++){
            this.typeNumbers[i]=0;
        }
        this.itself.beginFill(0x66CCFF);
        this.itself.zIndex = 5;
        this.itself.position.set(this.startX,this.startY);
        this.itself.drawRect(0,0,classTile.Tile.lengthX*7,classTile.Tile.lengthY);
        gameStage.itself.addChild(this.itself);
    },
    toBarPosition(value){
        return classTile.Tile.lengthX*(value-1);
    },
    refresh(){
        for(let i=1;i<= this.lengthNow;i++){
            this.tileLists[i].itself.position.set(bar.toBarPosition(i),0);
        }
    },
    checkMatch(){
        console.log('before:');
        console.log(bar.typeNumbers);
        console.log(bar.tileLists);
        for(let i=1;i<=classTile.Tile.types;i++){
            if(bar.typeNumbers[i]>=3){
                while(bar.typeNumbers[i]!==0){
                    for(let tile of bar.tileLists){
                        if(tile.id ===i){
                            tile.removeFromBar();
                            break;
                        }
                    }
                }
                scoreBoard.add(100);
            }
        }
        bar.refresh();
        console.log('after:');
        console.log(bar.typeNumbers);
        console.log(bar.tileLists);
    }
}

let scoreBoard={
    startX : 0,
    startY : classTile.Tile.lengthY/2,
    itself : new PIXI.Text(),
    score : 0,
    create(){
        this.itself.position.set(this.startX,this.startY);
        gameStage.itself.addChild(this.itself);
        this.itself.text = `分数：${this.score}`;
    },
    add(value){
        this.score += value;
        this.itself.text = `分数：${this.score}`;
    }
}

let skillArea ={
    startX : 0,
    startY : window.innerHeight/8,
    itself : new PIXI.Graphics(),
    create() {
        this.itself.beginFill(0x66CCFF);
        this.itself.zIndex =5;
        this.itself.position.set(this.startX,this.startY);
        this.itself.drawRect(0,0,classTile.Tile.lengthX*6,classTile.Tile.lengthY*4);
        gameStage.itself.addChild(this.itself);
    }

}
export{gameMap,gameStage,bar,scoreBoard,skillArea}