import * as PIXI from 'pixi.js'
import * as gameObjects from './gameObjects';
import * as Images from './images'
//import * as systemValues from './systemValues';

class Tile {
    static types =16;
    static lengthX = 118*0.5;
    static lengthY = 133*0.5;
    constructor(id) {
        if(id===0){
            do{

                this.id = Math.floor(Math.random()*Tile.types+1);
            }while(gameObjects.gameMap.checkId(this.id));
        }else {
            this.id = id;
        }
    }
    static toMapX(x){
        return (x-1)*Tile.lengthX;
    }
    static toMapY(y){
        return (y-1)*Tile.lengthY;
    }
    createToMap(X,Y){
        this.x=X;
        this.y=Y;
        this.itself = new PIXI.Sprite.from(Images[`tile${this.id}`]);
        this.itself.position.set(Tile.toMapX(this.x),Tile.toMapY(this.y));
        this.itself.scale.set(0.5,0.5);
        this.itself.interactive = true;
        this.itself.zIndex=10;
        gameObjects.gameMap.tileLists.set(`${this.x},${this.y}`,this);
        this.itself.on('pointertap', ()=>{
            this.moveToBar();
            gameObjects.gameMap.fallAndCreate(this.x,this.y);
            gameObjects.bar.checkMatch();
        });
        gameObjects.gameMap.typeNumbers[this.id]++;
        gameObjects.gameMap.itself.addChild(this.itself);
    }
    moveToBar(){
        gameObjects.bar.lengthNow++;
        gameObjects.gameMap.itself.removeChild(this.itself);
        this.itself.interactive=false;
        this.itself.position.set(gameObjects.bar.toBarPosition(gameObjects.bar.lengthNow),0);
        gameObjects.bar.itself.addChild(this.itself);
        let addPosition = gameObjects.bar.lengthNow;
        for(let i=1;i<gameObjects.bar.lengthNow;i++){
            if(gameObjects.bar.tileLists[i].id===this.id){
                addPosition =i;
            }
        }
        gameObjects.bar.tileLists.splice(addPosition,0,this);
        gameObjects.bar.refresh();
        gameObjects.bar.typeNumbers[this.id]++;
        gameObjects.gameMap.typeNumbers[this.id]--;
    }
    removeFromBar(){
        let tempPosition=1;
        for(let i=1;i<=gameObjects.bar.lengthNow;i++){
            if(gameObjects.bar.tileLists[i].id===this.id){
                tempPosition = i;
                break;
            }
        }
        gameObjects.bar.tileLists.splice(tempPosition,1);
        gameObjects.bar.itself.removeChild(this.itself);
        gameObjects.bar.lengthNow--;
        let id = this.id;
        gameObjects.bar.typeNumbers[id]--;
        //gameObjects.bar.refresh();
    }

}

export{Tile};